@import url(https://fonts.googleapis.com/css?family=Boogaloo);
/* theme vars */
body {
  background: #FFF1D0;
  margin: 0 auto;
  max-width: 550px;
  font-family: "Boogaloo", sans-serif;
}
/* overall container */
.boggle {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.boggle h1 {
    color: #009B72;
    text-align: center;
}
/* wrapper around tiles */
.board {
  background: #009DDC;
  border-radius: 35px;
  border: 8px solid #F0C808;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  width: 335px;
}
@media (min-width: 900px) {
.board {
      width: 100%;
}
}
/* individual tile */
.tile {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  color: black;
  font-size: 60px;
  border-radius: 15px;
  background: #e8fff9;
  height: 70px;
  width: 70px;
  margin: 5px;
}
@media (min-width: 900px) {
.tile {
      height: 100px;
      width: 100px;
      margin: 10px;
}
}
/* wrapper around timer + button */
.controls {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  width: 100%;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  margin-top: 15px;
}
/* reset button */
.shake {
  color: #F0C808;
  background: #009DDC;
  border: 4px solid #F0C808;
  border-radius: 15px;
  height: 100px;
  width: 200px;
  font-family: "Boogaloo", sans-serif;
  font-size: 60px;
}
.shake:focus {
    outline: none;
}
.stats {
  color: black;
}
/* timeWarning applied class */
.red {
  color: red;
}
.round {
  font-size: 36px;
  margin: 0;
}
.round span {
    color: #009B72;
}
.timer {
  font-size: 72px;
  margin: 0;
}
